.oneliner-container{
  min-height: 50vh;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  h1{
    color: $brand-primary;
    @include fp(font-size, 14, 36);
    margin-bottom: 50px;
  }
}

.oneliner-success-message{
  position: fixed;
  top: 60px;
  left: 20px;
  right: 20px;
  @include bounceInDown();
}

.oneliner-form{
  max-width: 450px;
  margin: 0 auto;
}