}

@media (min-width: 1920px) {
  nav.main ul li a {
    font-size: 18px;
  }
}

* {
  font-family: 'Ubuntu', sans-serif;
}

html, body {
  margin: 0;
  padding: 0;
  background: #1f2022;
  color: #fff;
}

body {
  padding-top: 80px;
}

.md-input-container:after {
  background-color: rgba(255, 255, 255, 0.12);
}

.counter {
  text-align: center;
  font-weight: 100;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
}

.counter h1 {
  color: #c24d2e;
  font-weight: 100;
  font-size: calc(1.875vw + 18px);
  margin: 40px 0px 20px;
}

@media (max-width: 320px) {
  .counter h1 {
    font-size: 24px;
  }
}

@media (min-width: 1920px) {
  .counter h1 {
    font-size: 54px;
  }
}

.counter #clockdiv {
  color: #fff;
  display: inline-block;
  font-weight: 400;
  text-align: center;
  font-size: calc(4.375vw + 16px);
}

@media (max-width: 320px) {
  .counter #clockdiv {
    font-size: 30px;
  }
}

@media (min-width: 1920px) {
  .counter #clockdiv {
    font-size: 100px;
  }
}

.counter #clockdiv > div {
  padding: 1vw;
  border-radius: 3px;
  background: #24252a;
  display: inline-block;
}

.counter #clockdiv div > span {
  padding: 1vw;
  border-radius: 3px;
  background: #c24d2e;
  display: flex;
  align-items: center;
  justify-content: center;
  min-width: calc(6.25vw + 10px);
  min-height: calc(6.25vw + 10px);
}

@media (max-width: 320px) {
  .counter #clockdiv div > span {
    min-width: 30px;
  }
}

@media (min-width: 1920px) {
  .counter #clockdiv div > span {
    min-width: 130px;
  }
}

@media (max-width: 320px) {
  .counter #clockdiv div > span {
    min-height: 30px;
  }
}

@media (min-width: 1920px) {
  .counter #clockdiv div > span {
    min-height: 130px;
  }
}

.counter .smalltext {
  padding-top: 5px;
  font-size: calc(0.875vw + 11.2px);
}

@media (max-width: 320px) {
  .counter .smalltext {
    font-size: 14px;
  }
}

@media (min-width: 1920px) {
  .counter .smalltext {
    font-size: 28px;
  }
}

.counter .legend {
  font-size: calc(0.25vw + 11.2px);
  color: rgba(255, 255, 255, 0.5);
  margin-top: 5vh;
  font-weight: 400;
}

@media (max-width: 320px) {
  .counter .legend {
    font-size: 12px;
  }
}

@media (min-width: 1920px) {
  .counter .legend {
    font-size: 16px;
  }
}

.counter .legend a, .counter .legend p {
  color: rgba(255, 255, 255, 0.5);
  line-height: 1.4;
  text-decoration: none;
}

.md-card .md-title {
  font-size: calc(0.5vw + 10.4px);
}

@media (max-width: 320px) {
  .md-card .md-title {
    font-size: 12px;
  }
}

@media (min-width: 1920px) {
  .md-card .md-title {
    font-size: 20px;
  }
}

.oneliner-container {
  min-height: 50vh;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.oneliner-container h1 {
  color: #FF5722;
  font-size: calc(1.375vw + 9.6px);
  margin-bottom: 50px;
}

@media (max-width: 320px) {
  .oneliner-container h1 {
    font-size: 14px;
  }
}

@media (min-width: 1920px) {
  .oneliner-container h1 {
    font-size: 36px;
  }
}

.oneliner-success-message {
  position: fixed;
  top: 60px;
  left: 20px;
  right: 20px;
  -webkit-animation-name: bounceInDown;
  -moz-animation-name: bounceInDown;
  -ms-animation-name: bounceInDown;
  -o-animation-name: bounceInDown;
  animation-name: bounceInDown;
  -webkit-animation-iteration-count: 1s;
  -moz-animation-iteration-count: 1s;
  -ms-animation-iteration-count: 1s;
  -o-animation-iteration-count: 1s;
  animation-iteration-count: 1s;
  -webkit-animation-duration: 1s;
  -moz-animation-duration: 1s;
  -ms-animation-duration: 1s;
  -o-animation-duration: 1s;
  animation-duration: 1s;
  -webkit-animation-delay: 0s;
  -moz-animation-delay: 0s;
  -ms-animation-delay: 0s;
  -o-animation-delay: 0s;
  animation-delay: 0s;
  -webkit-animation-timing-function: ease;
  -moz-animation-timing-function: ease;
  -ms-animation-timing-function: ease;
  -o-animation-timing-function: ease;
  animation-timing-function: ease;
  -webkit-animation-fill-mode: both;
  -moz-animation-fill-mode: both;
  -ms-animation-fill-mode: both;
  -o-animation-fill-mode: both;
  animation-fill-mode: both;
  -webkit-backface-visibility: hidden;
  -moz-backface-visibility: hidden;
  -ms-backface-visibility: hidden;
  -o-backface-visibility: hidden;
  backface-visibility: hidden;
}

.oneliner-form {
  max-width: 450px;
  margin: 0 auto;
}
